import React, { useState, useMemo, useEffect, memo } from "react";
import { useDispatch, useSelector } from "react-redux";
import LoadingOverlay from "react-loading-overlay";
import { Button, Typography } from "@material-ui/core";
import {
  addEmployee,
  removeEmployee,
  removeAllEmployee
} from "./utils/actions";
import { PageContainer } from "./styles/PageContainer";
import { EmployeeContainer } from "./styles/EmployeeContainer";
import { Table } from "./components/Table";
import { InputTextField } from "./components/InputTextField";

const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

const App = memo(() => {
  const dispatch = useDispatch();
  const { employees } = useSelector((state) => {
    return { employees: state.employees };
  });
  const [inputName, setInputName] = useState("");
  const [inputLastName, setInputLastName] = useState("");
  const [inputAge, setInputAge] = useState("");
  const [data, setData] = useState(employees);
  const [isOverlayActive, setIsOverlayActive] = useState(false);
  const columns = useMemo(
    () => [
      {
        minWidth: 178,
        resizable: true,
        Header: "Имя",
        accessor: "firstName"
      },
      {
        minWidth: 177,
        resizable: true,
        Header: "Фамилия",
        accessor: "lastName"
      },
      {
        minWidth: 177,
        resizable: true,
        Header: "Возраст",
        accessor: "age"
      },
      {
        id: "delete",
        accessor: () => "delete",
        width: 130,
        Header: (tableProps) => {
          const { isAllRowsSelected } = tableProps;
          if (isAllRowsSelected) {
            return (
              <Button
                size="small"
                variant="contained"
                color="secondary"
                onClick={() => {
                  setIsOverlayActive(true);
                  delay(2000).then(() => {
                    dispatch(removeAllEmployee());
                    setIsOverlayActive(false);
                  });
                }}
              >
                Удалить всё
              </Button>
            );
          }
          return null;
        },
        Cell: (tableProps) => (
          <Button
            size="small"
            variant="contained"
            color="secondary"
            onClick={() => {
              const { index: rowIndex } = tableProps?.row;
              const dataId = tableProps?.data[rowIndex]?.id;
              const { rows } = tableProps;
              const selectedRows = rows
                .filter((row) => {
                  const isSelected = row.isSelected;
                  if (isSelected) return row.original.id;
                  return false;
                })
                .map((row) => row.original.id);
              if (dataId) {
                setIsOverlayActive(true);
                delay(2000).then(() => {
                  dispatch(removeEmployee(dataId, rowIndex, selectedRows));
                  setIsOverlayActive(false);
                });
              }
            }}
          >
            Удалить
          </Button>
        )
      }
    ],
    []
  );

  const handleCreateEmployee = () => {
    setIsOverlayActive(true);
    delay(2000).then(() => {
      dispatch(addEmployee(inputName, inputLastName, inputAge));
      setIsOverlayActive(false);
    });
    setInputName("");
    setInputLastName("");
    setInputAge("");
  };
  const handleOnChange = (value, func) => {
    func(value);
  };

  useEffect(() => {
    setData(employees);
  }, [employees]);

  return (
    <PageContainer>
      <LoadingOverlay
        active={isOverlayActive}
        spinner
        text="Ожидайте результата действия..."
        styles={{
          wrapper: {
            position: "fixed",
            width: "100vw",
            height: "100vh",
            padding: "0",
            transition: "all .3s",
            fontFamily: "Roboto",
            fontSize: "30px"
          },
          overlay: {
            position: "fixed",
            width: "100vw",
            height: "100vh",
            top: 0,
            left: 0,
            display: "flex",
            textAlign: "center",
            fontSize: "1.2em",
            color: "#fff",
            background: "rgba(0,0,0,0.7)",
            zIndex: 800,
            transition: "opacity 500ms ease-in",
            opacity: 1
          }
        }}
      >
        <Typography variant="h5" gutterBottom>
          Создать сотрудника:
        </Typography>
        <EmployeeContainer>
          <InputTextField
            required
            id="name"
            label="Имя"
            variant="outlined"
            value={inputName}
            func={setInputName}
            handleOnChange={(value, func) => handleOnChange(value, func)}
          />
          <InputTextField
            required
            id="last-name"
            label="Фамилия"
            variant="outlined"
            value={inputLastName}
            func={setInputLastName}
            handleOnChange={(value, func) => handleOnChange(value, func)}
          />
          <InputTextField
            required
            type="number"
            id="age"
            label="Возраст"
            variant="outlined"
            value={inputAge}
            func={setInputAge}
            handleOnChange={(value, func) => handleOnChange(value, func)}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleCreateEmployee}
            disabled={!inputName || !inputLastName || !inputAge}
          >
            Создать
          </Button>
        </EmployeeContainer>
        <Table columns={columns} data={data} />
      </LoadingOverlay>
    </PageContainer>
  );
});

export default App;
