import React from "react";
import {
  useTable,
  useRowSelect,
  useResizeColumns,
  useBlockLayout,
  useSortBy
} from "react-table";
import { Typography } from "@material-ui/core";
import ReorderTwoToneIcon from "@material-ui/icons/ReorderTwoTone";
import { IndeterminateCheckbox } from "./IndeterminateCheckbox";

export const Table = ({ columns, data }) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    selectedFlatRows
  } = useTable(
    {
      columns,
      data
    },
    useResizeColumns,
    useBlockLayout,
    (hooks) => {
      hooks.visibleColumns.push((columns) => [
        {
          id: "selection",
          width: 50,

          Header: ({ getToggleAllRowsSelectedProps }) => (
            <div>
              <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
            </div>
          ),

          Cell: ({ row }) => (
            <div>
              <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
            </div>
          )
        },
        ...columns
      ]);
    },
    useSortBy,
    useRowSelect
  );

  const selectedNames = selectedFlatRows.map((item, index) =>
    index === selectedFlatRows.length - 1
      ? item?.values?.firstName
      : item?.values?.firstName + ", "
  );

  return (
    <>
      <table {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column, columnIndex) => {
                const classForCellWithChexbox = !columnIndex
                  ? "cell-with-checkbox"
                  : "";
                const lastTh = columnIndex === headerGroup.headers.length - 1;

                return (
                  <th
                    {...column.getHeaderProps()}
                    className={classForCellWithChexbox}
                  >
                    {column.resizable && (
                      <div
                        {...column.getResizerProps()}
                        className={`resizer ${
                          column.isResizing ? "isResizing" : ""
                        }`}
                      >
                        <ReorderTwoToneIcon />
                      </div>
                    )}

                    {!lastTh ? (
                      <span
                        {...column.getHeaderProps(
                          column.getSortByToggleProps()
                        )}
                        className="sorted-container"
                      >
                        {column.isSorted
                          ? column.isSortedDesc
                            ? column.render("Header") + " 🔽"
                            : column.render("Header") + " 🔼"
                          : column.render("Header")}
                      </span>
                    ) : (
                      column.render("Header")
                    )}
                  </th>
                );
              })}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            const { isSelected } = row?.original;
            const classForSelectedRow =
              isSelected || row.isSelected ? "row-selected" : "";

            prepareRow(row);
            return (
              <tr {...row.getRowProps()} className={classForSelectedRow}>
                {row.cells.map((cell, cellIndex) => {
                  const classForCellWithChexbox = !cellIndex
                    ? "cell-with-checkbox"
                    : "";
                  return (
                    <td
                      className={classForCellWithChexbox}
                      {...cell.getCellProps()}
                    >
                      {cell.render("Cell")}
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
        <tfoot>
          <tr>
            <td colSpan="4">
              <Typography color="secondary">Пользователи:</Typography>
              <Typography color="primary">{selectedNames}</Typography>
            </td>
          </tr>
        </tfoot>
      </table>
    </>
  );
};
